import React, { useState, useEffect } from 'react';
import ReactDOM from 'react-dom';
import Button from 'react-bootstrap/Button';
import "../css/Menu.css";
import { ButtonGroup } from 'react-bootstrap';
import { Link } from "gatsby";
import logo from '../images/smu.png';
import { createBrowserHistory } from "history";

function select() {
    const [classSelect, setClass] = useState('');

    const history = createBrowserHistory();

    const directToSuccess = () => {
        history.push('/classevalsuccess');
        window.location.reload();
    }

    const handleSubmit = (e) => {
        directToSuccess();
    }

    return (
        <div id="stuff">
            <em>Please Select the Class to Schedule Peer Evaluations For</em>
            <label class="name">Class Name - Class ID</label>
            <select name="classSelect" id="classSelect" onChange={(e) => setClass(e.target.value)}>
                <option value="default" selected="true">Choose a class.</option>
                <option value="classes">Managing Risk with Derivatives, Professor X, 85098, Section 1</option>
                <option value="classes">Business Analysis, Professor Y, 89503, Section 2</option>
                <option value="classes">Networks Telecommunication, Professor Z, 47329, Section 3b</option>
            </select>
            <div></div><br/>

            <Button color="black" type="submit" onClick={handleSubmit}>Schedule</Button>
            <Button variant="outline-light" size="lg"><Link to="/professormenu">Back</Link></Button>
        </div>
    )
}

export default select;